import React from 'react';
import '../signup.css';
import {connect} from 'react-redux';
import {SetCurrentuser} from "../redux/user/User.action";
import {registerUser } from '../redux/user/User.action';

class SignIn extends React.Component{
    constructor(props){
        super(props);
    }

    userData= async (e)=>{
      e.preventDefault();
      console.log(this.props.user);
      this.props.registerUser(this.props.user);
    }

    render(){
        return(
            <form id="sign_in" style={{paddingLeft: "12%", paddingRight: "12%"}} onSubmit={this.userData}>
                <input type="text" placeholder="Username" value={this.props.user.username} className="email" onChange={(e) => this.props.SetCurrentuser({user:{...this.props.user,username:e.target.value}})}/>  
                <input type="password" placeholder="Password" value={this.props.user.password} className="password" onChange={(e) => this.props.SetCurrentuser({user:{...this.props.user,password:e.target.value}})}/>
                        {this.props.invalid && <p style={{color:"red"}}>Invalid Username or Password</p>}
                <p>Forgot your password?</p>
                <button id="b1">Sign In</button>
            </form>
        )
    }
}

const mapStateToProps = state =>({
    user:{
        username:state.user.user.username,
        password: state.user.user.password,
    },
    invalid: state.user.invalid
});

const mapDispatchToProps = dispatch =>({
    SetCurrentuser: user => dispatch(SetCurrentuser(user)),
    registerUser: user => dispatch(registerUser(user))
})

export default connect(mapStateToProps,mapDispatchToProps)(SignIn);