import React, {Component, Fragment} from 'react';
import './Otp.css';
import image from './OTP anime.gif';
import { Button,Card } from 'react-bootstrap';
import PhoneNo from './components/PhoneNo';
import Verify from './components/Verify';
import axios from 'axios';

class Otp extends Component {
    constructor(props){
        super(props);

        this.state={
            verify:false,
            user:{
                number:""
            },
            session_id:""
        }
    }
    handleChange=(event)=>{
        this.setState({user:{number:event.target.value}});
        console.log(this.state.user);
    }
    Generate()
    {
        window.alert("New One Time Password has been sent");
        document.getElementById('sent').innerHTML = "Enter The new Code that We sent You";
    }
    sendData=(e)=>{
        e.preventDefault();
        console.log(this.state.user);

        if(this.state.user.number.length===10)
        {
            axios.post("https://incampusbackend.herokuapp.com/api/v1/userinfo",this.state.user)
            .then(response=>{
                console.log(response);
                if(response.message==="Success")
                {
                    this.setState({verify:true});
                    console.log(this.state.verify);
                }
                this.setState({session_id:response.details});
            }).catch(error=>{
                console.log(error);
                if(error)
                {
                    this.setState({verify:true});
                    console.log(this.state.verify);
                }
            })
        }          
    }
    render(){
        return(
            <Fragment>
                <div className="outer">
                    <div className="middle">
                        <Card id="c">
                            <Card.Body>
                                <h5 className="card-title" id="heading">Verification</h5>
                                <img src={image} width = '85%' />
                                { !this.state.verify && <PhoneNo verfiy={this.state.verify} sendData={this.sendData} handleChange={this.handleChange}/>}
                                <Verify verify={this.state.verify} session_id={this.state.session_id}/>
                            </Card.Body>
                        </Card>
                    </div>
                </div>
            </Fragment>
        )
    }
}

export default Otp;