import React, {Component} from 'react';
import { Button } from 'react-bootstrap';
import $ from 'jquery';
import axios from 'axios';

class Verify extends Component{
    constructor(props){
        super(props);
        this.state={
            s1:"",
            s2:"",
            s3:"",
            s4:"",
            s5:"",
            s6:"",
            otp:"",
        }
    }
    moveNext = (e) => {
        console.log(e.target.id);
        setTimeout(function(){ console.log($(e.target).value); }, 500);
        if(e.target.value === "") {
            
            $(e.target).prev().focus();
        } else {
            $(e.target).next().focus();
        }
    }
    userData=(e)=>{
      e.preventDefault();
      console.log(this.state.s2,this.state.s3,this.state.s4,this.state.s5,this.state.s6);
      axios.post("https://incampusbackend.herokuapp.com/api/v1/verifyotp",{
          otp:this.state.s1+this.state.s2+this.state.s3+this.state.s4+this.state.s5+this.state.s6,
      })
      .then(response=>{
          console.log(response);
      }).catch(error=>{
          console.log(error);
      })

    }
    render(){
        if(this.props.verify)
        {
            return(
                <div>
                    <div className="card-text" id="sent" >Enter the verification Code we just sent you on your phone...</div>
                    <form type="submit" onSubmit={this.userData}>
                        <input className="input" type="text" maxlength="1" onKeyUp={this.moveNext} id="input1" onChange={(e) =>  this.setState({s1:e.target.value}) }/>
                        <input className="input" type="text" maxlength="1" id="input2" onKeyUp={this.moveNext} onChange={(e) =>  this.setState({s2:e.target.value}) }/>
                        <input className="input" type="text"  maxlength="1" id="input3" onKeyUp={this.moveNext} onChange={(e) =>  this.setState({s3:e.target.value}) }/>
                        <input className="input" type="text" maxlength="1" id="input4" onKeyUp={this.moveNext} onChange={(e) =>  this.setState({s4:e.target.value}) }/>
                        <input className="input" type="text" maxlength="1" id="input5" onKeyUp={this.moveNext} onChange={(e) =>  this.setState({s5:e.target.value}) }/>
                        <input className="input" type="text" maxlength="1" id="input6" onKeyUp={this.moveNext} onChange={(e) =>  this.setState({s6:e.target.value}) }/>
                        <br /><br /><br /><br />
                    <div id="resend">If you didn't recieve a code! <a href="#" onClick={this.Generate} >Resend</a> </div>
    
                    <Button type="submit" className="btn btn-success" id="success"><a href="/profile" style= {{color:"white",textDecoration:"none"}}>Verify</a></Button>
                    </form>
                </div>
            )
        }
        else{
            return(null);
        }
    }
}

export default Verify;