import React from 'react';
import '../signup.css';
import axios from 'axios';
import {connect} from 'react-redux';
import {SetCurrentuser,signupUser} from "../redux/user/User.action";

class SignUp extends React.Component{
    constructor(props){
        super(props);
    }

        userData=(e)=>{
      e.preventDefault();
      console.log(this.props.user);
      this.props.signupUser(this.props.user);
    }
    
render(){
    return (
        <form id="sign_up" type="submit" style={{paddingLeft: "12%", paddingRight: "12%"}} onSubmit={this.userData}>
            <input type="text" placeholder="Username" id="name" name="username" onChange={(e) =>  this.props.SetCurrentuser({user:{...this.props.user,username:e.target.value}}) }/>
            <input type="email" placeholder="Email" className="email" name="email" onChange={(e) =>  this.props.SetCurrentuser({user:{...this.props.user,email:e.target.value}}) }/>
            <input type="password" placeholder="Password" className="password" name="password" onChange={(e) =>  this.props.SetCurrentuser({user:{...this.props.user,password:e.target.value}}) }/>
            <p>Forgot your password?</p>
            <button id="b4" type="submit"><a id="opt" style={{color: "white", textDecoration: "none"}} href="\otp">Sign Up</a></button>
        </form>
    );
}
}

const mapStateToProps = state =>({
    user:{
        username:state.user.user.username,
        email:state.user.user.email,
        password:state.user.user.password
    }
});

const mapDispatchToProps = dispatch =>({
    SetCurrentuser: user => dispatch(SetCurrentuser(user)),
    signupUser: user => dispatch(signupUser(user))
})

export default connect(mapStateToProps,mapDispatchToProps)(SignUp);