import axios from 'axios';

export const SetCurrentuserStart = () =>({
    type: 'FETCH_START',
});

export const SetCurrentuser = (user) =>({
    type: 'SET_CURRENT_USER',
    payload: user
})

export const Invalid = () =>({
    type: 'Invalid',
})


export const SetCurrentTest = (user) =>({
    type: 'SET_CURRENT_TEST',
    payload: user
})

export const SetCurrentuserEnd = (message) =>({
    type: 'FETCH_FAILURE',
    payload: message
})


export const registerUser = (user) => dispatch => {
    console.log(user);
    axios.post("https://incampusbackend.herokuapp.com/api/v1/login",user)
    .then(response=>{
        //console.log('haha');
        console.log(response);
        axios.defaults.headers.common['Authorization'] = `Bearer ${response.data.accessToken}`;
        dispatch(SetCurrentTest(user));
    }).catch(error=>{
        console.log(error);
        //console.log('haha');
        dispatch(SetCurrentuserEnd(error));
        if(error)
        {
            dispatch(Invalid());
        }
    });
    //console.log('haha2');
}

export const signupUser = (user) => dispatch => {
    console.log(user);
    axios.post("https://incampusbackend.herokuapp.com/api/v1/register",user)
    .then(response=>{
        //console.log('haha');
        console.log(response);
        axios.defaults.headers.common['Authorization'] = `Bearer ${response.data.accessToken}`;
        dispatch(SetCurrentTest(user));
    }).catch(error=>{
        console.log(error);
        //console.log('haha');
        dispatch(SetCurrentuserEnd(error));
    });
    //console.log('haha2');
}