const INITIAL_STATE = {
    isFetching:false,
    user:{
        username: '',
        password: '',
        email:''
    },
    test:{},
    errorMessage:"",
    invalid: false
}

const userReducer = (state = INITIAL_STATE , action) => {
    switch(action.type){
        case 'FETCH_START':
            return{
                ...state,
                isFetching: true
            }    
        case 'SET_CURRENT_USER': 
        return{
            ...state,
            isFetching:false,
            user:{
                username: action.payload.user.username,
                password: action.payload.user.password,
                email:action.payload.user.email
            },
            invalid:false
        }
        case 'SET_CURRENT_TEST': 
        return{
            ...state,
            test:action.payload
        }
        case 'FETCH_FAILURE':
            return{
                ...state,
                isFetching: false,
                errorMessage: action.payload
            }
        case 'Invalid':
            return{
                ...state,
                user:{
                    username: "",
                    password: ""
                },
                invalid:true
            }
        default: return state;
    };
}
export default userReducer;