import React, {Component} from 'react';
import '../signup.css';
import { Button } from 'react-bootstrap';
import axios from 'axios';

class PhoneNo extends Component{
    constructor(props){
        super(props);
        this.state={
            user: {
                number:"",
           }
        }
    }

    render(){
        if(!this.props.verify)
        {
            return(
                <div>
                <form id="verifyPh" type="submit">
                        <input type="text" placeholder="Phone Number" className="number" name="number" maxlength="10" minlength="10" onChange={this.props.handleChange} />
                        <button className="btn btn-success" onClick={this.props.sendData}>Click To Send Mobile No.</button>
                </form>
               </div> 
           )
        }
        else{
            return(null);
        }
    }
}

export default PhoneNo